import Decoder from './decoder';
import Player from './player';

export default class Flvplayer {
    constructor(options) {
        this.options = Object.assign({}, Flvplayer.options, options);
        this.decoder = new Decoder(this);
        this.player = new Player(this);
    }

    static get options() {
        return {
            url: '',
            canvas: null,
            width: null,
            height: null
        };
    }

    
}
